<template>
  <transition name="search">
    <div class="search">
      <header class="g-header-container">
        <search-header @query="getQuery"></search-header>
      </header>
      <div class="g-content-container">
        <lu-scroll>
          <search-hot
            v-if="!query"
          ></search-hot>
          <search-history
            @show-confirm="showConfirm"
            ref="history"
            v-if="!query"
          ></search-history>
          <search-result
            v-show="query"
            :query="query"
          ></search-result>
        </lu-scroll>
      </div>
      <lu-confirm
        msg="确定清空吗？"
        ref="confirm"
        @confirm="clearAllSearchHistory"
      ></lu-confirm>
    </div>
  </transition>
</template>

<script>
  import SearchHeader from './header.vue';
  import SearchHistory from './history.vue';
  import SearchHot from './hot.vue';
  import SearchResult from './result.vue';
  import LuScroll from '@/base/scroll';
  import LuConfirm from '@/base/confirm';
  export default {
    name: 'Search',
    components: {
      SearchHeader,
      SearchHistory,
      SearchHot,
      SearchResult,
      LuScroll,
      LuConfirm
    },
    data () {
      return {
        query: ''
      };
    },
    methods: {
      showConfirm() {
        this.$refs.confirm.show();
      },
      clearAllSearchHistory() {
        this.$refs.history.clear();
        this.$refs.history.update();
      },
      getQuery(query) {
        this.query = query;
      }
    }
  };
</script>

<style lang="scss" scoped>
@import '~@/assets/scss/mixins';
  .search{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: $search-z-index;
    background-color: $bgc-theme;
  }
  .search-enter-active,
  .search-leave-active{
    transition: all 0.3s;
  }
  .search-enter,
  .search-leave-to{
    transform: translate3d(100%, 0, 0);
  }
  .g-content-container{
    position: absolute;
    top: 50px;
    height: 100%;
    width: 100%;
  }
</style>



