
import {SEARCH_HISTORY_KEY} from '@/pages/search/config.js';
import storage from '@/assets/js/storage';

export const searchMixins = {
  methods: {
    $_search_selectItem(keyword) {
      let keywords = storage.get(SEARCH_HISTORY_KEY, []);
      if (keywords.length !== 0) {
        keywords = keywords.filter(val => val !== keyword);
      }
      keywords.unshift(keyword);
      storage.set(SEARCH_HISTORY_KEY, keywords);
      // 跳转到第三方网站去
    }
  }
};



