<template>
  <lu-navbar class="header">
    <i class="iconfont icon-back" slot="left" @click="goBack"></i>
    <lu-search-box
      slot="center"
      placeholder="情人节巨惠"
      @query="query"
    ></lu-search-box>
  </lu-navbar>
</template>

<script>
  import LuNavbar from '@/base/navbar';
  import LuSearchBox from '@/base/search-box';
  export default {
    name: 'SearchHeader',
    components: {
      LuNavbar,
      LuSearchBox
    },
    methods: {
      query(query) {
        this.$emit('query', query);
      },
      goBack() {
        this.$router.back();
      }
    }
  };
</script>
<style lang="scss" scoped>
  @import "~@/assets/scss/mixins";
  .header {
    border-bottom: 1px solid $border-color;
    .iconfont{
      color: $icon-color;
      font-size: $icon-font-size;
    }
    .mine-search-box-wrapper {
      background-color: #f7f7f7;
    }
  }
</style>



